import React, { useCallback } from 'react';
import Dropzone from 'react-dropzone';
import PropTypes from 'prop-types';
import "./UploadPic.css";


const toBase64 = file => new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = (event) => {
        resolve(event.target.result);
    };
    reader.onerror = (error) => {
        reject(error);
    };

    reader.readAsDataURL(file);
});


/*
 * Renders a drag and drop picture upload component
*/
const UploadPic = (props) => {

    // picture stores the image file details
    const { picture, setPicture } = props;

    const handleDrop = useCallback(async (acceptedFiles) => {
        const base64EncodedImage = await toBase64(acceptedFiles[0]);
        setPicture(base64EncodedImage);
    }, []);


    return (
        <div>
            <Dropzone
                onDrop={handleDrop}
                accept="image/*"
            >
                {({ getRootProps, getInputProps }) => (
                    <div {...getRootProps({ className: "dropzone_upload" })}>
                        <input {...getInputProps()} />
                        <span className="spam_upload">{"ðŸ“‚"}</span>
                        <p>Drag'n'drop profile picture, or click to select image file</p>
                    </div>
                )}
            </Dropzone>
            <div style={{ color: "white" }}>
                <strong>
                    picture:
                    {
                        picture ?
                            "File Uploaded" :
                            " Not uploaded"
                    }
                </strong>
            </div>
        </div>
    );
}

UploadPic.propTypes = {
    picture: PropTypes.object.isRequired,
    setPicture: PropTypes.func.isRequired,
};

export default UploadPic;
