import React, { useState } from 'react';
import { animated as a } from 'react-spring';
import { Container, Row } from 'reactstrap';
import Dropzone from 'react-dropzone';
import axios from 'axios';
import { BASE_URL } from '../../constants';
import { withRouter } from "react-router-dom";
import './UploadRecording.css'
import { Button } from 'reactstrap';

const UploadRecording = (props) => {
    // recording stores the image file details
    const [recordings, setRecordings] = useState([])

    // Sets the image file when dragged and dropped in the dropping zone
    const handleDrop = acceptedFiles => {
        setRecordings(acceptedFiles)
    }

    const onClick = () => {
        if (recordings.length == 6) {
            const userId = localStorage.getItem("Token")
            const eegRecordings = new FormData()
            recordings.map((file) => {
                eegRecordings.append("eeg_files", file)
            })

            axios.post(BASE_URL + "/eeg_recordings/register_eeg_recordings/" + userId, eegRecordings)
                .then((res) => {
                    console.log(res)
                    alert("Recordings uploaded")
                    window.location.href = "/"
                })
                .catch((err) => {
                    console.log(err)
                })
        }
        else{
            alert("Upload all files")
        }
    }

    return (
        <Container style={{ marginTop: "2rem", fontFamily:"Poppins, sans-serif" }}>
            <Dropzone
                onDrop={handleDrop}
                maxFiles={6}
            >
                {({ getRootProps, getInputProps }) => (
                    <div {...getRootProps({ className: "dropzone_upload_recording" })}>
                        <input {...getInputProps()} />
                        <span className="spam_upload_recording">{"ðŸ“‚"}</span>
                        {
                            recordings.length === 0 ?
                                (<p>Upload six EEG recording <br /> Drag'n'drop recordings, or click to select recording file</p>)
                                :
                                (<p>{recordings.length}Recordings Uploaded</p>)
                        }
                    </div>
                )}
            </Dropzone>
            <Button className="fillButton fill" onClick={onClick}>Submit</Button>
        </Container >
    );
}

export default withRouter(UploadRecording);