import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepContent from '@material-ui/core/StepContent';
import Button from '@material-ui/core/Button';
import { Icon, InlineIcon } from '@iconify/react';
import raisedFistMediumLightSkinTone from '@iconify-icons/emojione/raised-fist-medium-light-skin-tone';
import brainIcon from '@iconify-icons/noto/brain';
import footMediumLightSkinTone from '@iconify-icons/twemoji/foot-medium-light-skin-tone';
import ReactPlayer from 'react-player'
import { Card, CardBody, CardHeader, CardText } from 'reactstrap';
import demoVideo from '../../assets/demo_video.mp4'
import './VerticalStepper.css'

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        marginTop: '2rem',
        fontFamily: "Poppins, sans-serif"
    },
    button: {
        marginTop: theme.spacing(1),
        marginRight: theme.spacing(1),
    },
    actionsContainer: {
        marginBottom: theme.spacing(2),
    },
    resetContainer: {
        padding: theme.spacing(3),
    },
    stepIcon: {
        color: "#72f"
    },
    stepIconActive: {
        color: "#72f !important"
    },
    stepIconComplete: {
        color: "#72f !important"
    }
}));

function getSteps() {
    return ['Select campaign settings', 'Create an ad group', 'Create an ad'];
}


export default function VerticalStepper() {
    const classes = useStyles();
    const [activeStep, setActiveStep] = React.useState(0);
    const steps = getSteps();

    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleReset = () => {
        setActiveStep(0);
    };

    return (
        <div className={classes.root}>
            <Stepper activeStep={activeStep} orientation="vertical" style={{ backgroundColor: "black", color: "white" }}>
                <Step key={"Step 1"}>
                    <StepLabel StepIconProps={{
                        classes: { root: classes.stepIcon, active: classes.stepIconActive, completed: classes.stepIconComplete }
                    }}>Step 1</StepLabel>
                    <StepContent>
                        <Card style={{ color: "black", borderColor: "black" }}>
                            <CardHeader style={{ color: "#72f", backgroundColor: "black", fontWeight: "bold", fontSize: "2rem" }}>Step 1</CardHeader>
                            <CardBody style={{ backgroundColor: "white", color: "black" }}>
                                <Icon icon={brainIcon} width={100} height={100} />
                                <br />
                                    Wear Headgear
                                </CardBody>
                        </Card>
                        <div className={classes.actionsContainer}>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                    style={{ color: "white" }}
                                >
                                    Back
                                    </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Next
                                    </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>

                <Step key={"Step 2"}>
                    <StepLabel StepIconProps={{
                        classes: { root: classes.stepIcon, active: classes.stepIconActive, completed: classes.stepIconComplete }
                    }}>Step 2</StepLabel>
                    <StepContent>
                        <Card style={{ color: "black", borderColor: "black" }}>
                            <CardHeader style={{ color: "#72f", backgroundColor: "black", fontWeight: "bold", fontSize: "2rem" }}>Step 2</CardHeader>
                            <CardBody style={{ backgroundColor: "white", color: "black" }}>
                                <Icon icon={raisedFistMediumLightSkinTone} width={100} height={100} />
                                <br />
                                Close the fist on seeing an image on the top of the screen
                                </CardBody>
                        </Card>
                        <div className={classes.actionsContainer}>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                    style={{ color: "white" }}
                                >
                                    Back
                                    </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Next
                                    </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>

                <Step key={"Step 3"}>
                    <StepLabel StepIconProps={{
                        classes: { root: classes.stepIcon, active: classes.stepIconActive, completed: classes.stepIconComplete }
                    }}>Step 3</StepLabel>
                    <StepContent>
                        <Card style={{ color: "black", borderColor: "black" }}>
                            <CardHeader style={{ color: "#72f", backgroundColor: "black", fontWeight: "bold", fontSize: "2rem" }}>Step 3</CardHeader>
                            <CardBody style={{ backgroundColor: "white", color: "black" }}>
                                <Icon icon={footMediumLightSkinTone} width={100} height={100} />
                                <br />
                                Turn your foot inwards on seeing an image on the bottom of the screen
                                </CardBody>
                        </Card>
                        <div className={classes.actionsContainer}>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                    style={{ color: "white" }}
                                >
                                    Back
                                    </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Next
                                    </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>

                <Step key={"Step 4"}>
                    <StepLabel StepIconProps={{
                        classes: { root: classes.stepIcon, active: classes.stepIconActive, completed: classes.stepIconComplete }
                    }}>Step 4</StepLabel>
                    <StepContent>
                        <Card style={{ color: "black", borderColor: "black" }}>
                            <CardHeader style={{ color: "#72f", backgroundColor: "black", fontWeight: "bold", fontSize: "2rem" }}>Step 4</CardHeader>
                            <CardBody style={{ backgroundColor: "white", color: "black" }}>
                                <ReactPlayer url={demoVideo}
                                    controls={true} muted={true} width={400}
                                    height={400} style={{ marginLeft: "auto", marginRight: "auto" }} />
                                <br />
                                View Demo Video
                                </CardBody>
                        </Card>
                        <div className={classes.actionsContainer}>
                            <div>
                                <Button
                                    disabled={activeStep === 0}
                                    onClick={handleBack}
                                    className={classes.button}
                                    style={{ color: "white" }}
                                >
                                    Back
                                    </Button>
                                <Button
                                    variant="contained"
                                    color="primary"
                                    onClick={handleNext}
                                    className={classes.button}
                                >
                                    Finish
                                    </Button>
                            </div>
                        </div>
                    </StepContent>
                </Step>
            </Stepper>
            {activeStep === steps.length + 1 && (
                <Card style={{backgroundColor: "black", }}>
                    <CardBody>
                        <CardText style={{fontSize:"larger", fontWeight:"bold", color: "white" }}>All Steps Completed</CardText>
                        <Button onClick={handleReset} className={"fillButton fill"} style={{color: "white", border:"2px white solid"}}>
                            Reset
                        </Button>
                    </CardBody>
                </Card>
            )}
        </div>
    );
}
