import './App.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import SignUp from './pages/signUp/SignUp';
import Navbar from './components/navbar/Navbar';
import TaskPerformStepper from './pages/taskPerform/TaskPerformStepper';

function App() {
  return (
    <Router>
      <div className="App">
        <Navbar />
        <Switch>
          <Route exact path={"/signup.html/"} component={SignUp} />
          <Route exact path={"/signup.html/taskperform"} component={TaskPerformStepper} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
