import React, { useState } from 'react';
import { Form, FormGroup, Label, Input, Button, Card, Col, Row } from 'reactstrap';
import { BASE_URL } from '../../constants'
import { withRouter } from "react-router-dom";
import UploadPic from "../../components/uploadPic/UploadPic";
import axios from "axios";
import './SignUp.css'



const SignUp = (props) => {

    const [picture, setPicture] = useState(null)
    const [fname, setFname] = useState("")
    const [lname, setLname] = useState("")
    const [email, setEmail] = useState("")
    
    
    const onSubmit = (e) => {
        e.preventDefault()
        if (picture === null) {
            alert("Add a Profile Picture")
            return
        }
        var signUpForm = {}

        signUpForm["f_name"] = fname
        signUpForm["l_name"] = lname
        signUpForm["email"] = email
        signUpForm["profile_photo"] = picture


        console.log(signUpForm)

        axios.post(BASE_URL + "/user/register_user", { "user": signUpForm })
            .then((res) => {
                console.log(res.data.data.user_id)
                localStorage.setItem("Token", res.data.data.user_id);
                props.history.push("/signup.html/taskperform")
            })
            .catch((err) => {
                console.log(err)
                alert("Enter all fields")
            }
            )


    }
    return (
        <Card className="gradient_background py-1 my-2 mx-auto">
            <Form onSubmit={onSubmit} className="mx-5">

                <h1 style={{ textAlign: "center", marginBottom: "2rem", color: "white", fontWeight: "bold" }}>Registration</h1>
                <Row>
                    <Col sm="4">
                        <FormGroup className="my-3">
                            <Label style={{ color: "white", fontWeight: "bolder" }}>{"Profile Picture"}</Label>
                            <UploadPic picture={picture} setPicture={setPicture} />
                        </FormGroup>
                    </Col>
                    <Col>
                        <FormGroup className="my-3">
                            <Label style={{ color: "white", fontWeight: "bolder" , textAlign:"left", float:"left"}}>{"First Name"}</Label>
                            <Input type={"text"} onChange={event => setFname(event.target.value)}
                                value={fname} placeholder={"John"} required={true}>
                            </Input>
                        </FormGroup>

                        <FormGroup className="my-4">
                            <Label style={{ color: "white", fontWeight: "bolder", textAlign:"left", float:"left" }}>{"Last Name"}</Label>
                            <Input type={"text"} onChange={event => setLname(event.target.value)}
                                value={lname} placeholder={"Doe"} required={true}>
                            </Input>
                        </FormGroup>

                        <FormGroup className="my-4">
                            <Label style={{ color: "white", fontWeight: "bolder", textAlign:"left", float:"left" }}>{"Email"}</Label>
                            <Input type={"email"} onChange={event => setEmail(event.target.value)}
                                value={email} placeholder={"john.doe@xyz.com"} required={true}>
                            </Input>
                        </FormGroup>
                    </Col>
                </Row>
                <Button className="fillButton fill" >
                    Next
                </Button>
            </Form>
        </Card>
    );
};

export default withRouter(SignUp);
